import React, {
    Component
} from 'react';

import {
    Switch,
    Route
  } from "react-router-dom";

import Navigator from './Navigator';
import ProjectList from './ProjectList';
import Footer from './Footer';
import Project from './Project';

export class Body extends Component {
    constructor() {;
        super();

        this.state = {
            isSearching: true,
            projectList: [],
            showList: []
        };

        this.startSearching = this.startSearching.bind(this);
        this.loadList = this.loadList.bind(this);
        this.projectPages = this.projectPages.bind(this);
    };

    componentDidMount(){
        this.loadList();

    }

    loadList(){
        fetch('projects/projects.json').then((r) => {
            return r.json();
        }).then((json) => {
            console.log(json);
            this.setState({
                isSearching:this.state.isSearching,
                projectList:json,
                showList:json
            });
        });
    }

    projectPages(){
        console.log('ran showlist');

        return this.state.showList.map((pr,i) => {
            return <Route key={i} path={"/" + pr.dir}><Project project={pr}></Project></Route>;
        });
    }

    startSearching(value) {
        console.log(value);
        let newShow = [];
        this.state.projectList.forEach((p)=>{
            if(p.name.includes(value)){
                newShow.push(p);
            }else{
                var tagFound = false;
                p.tags.forEach((t) => {
                    if(t.includes(value)){
                        tagFound = true;
                    }
                });
                if(tagFound){
                    newShow.push(p);
                }
            }
        });
        newShow.sort();
        this.setState({
            isSearching:this.state.isSearching,
            projectList:this.state.projectList,
            showList:newShow
        });
    };

    render() {
        return ( <div className="main-body">
                <Switch>
                    {this.projectPages()}
                    <Route path="/about"><Project project={{
                        "name": "About",
                        "dir":"about",
                        "scripts":["scripts.js"]
                    }}></Project></Route>
                    <Route path="/contact"><Project project={{
                        "name":"Contact",
                        "dir":"contact",
                        "scripts":["scripts.js"]
                    }}></Project></Route>
                    <Route path="/">
                        <Navigator show={this.state.isSearching} onSearch={this.startSearching}></Navigator>
                        <ProjectList openPage={this.openPage} showList={this.state.showList}></ProjectList>
                    </Route>
                </Switch>
                    <Footer>  
                    </Footer>
        </div>
        )
    };
};

export default Body;