import React, {Component} from 'react'
import {Link} from 'react-router-dom';
//import $ from "jquery"; import postscribe from 'postscribe';

import closeIcon from '../../assets/img/closeIcon.png';

export class Project extends Component {
    
    constructor(props){
        super(props);
        this.fetchProject = this.fetchProject.bind(this);

        this.state = {
            'html':'',
        };

        this.fetchProject();
    }

    componentWillUnmount() {
        console.log('Clearing p5 Sketches');
        this.instance.innerHTML = '';
        var newS = document.createElement('script');
                newS.type = 'text/javascript';
                newS.async = true;
                newS.innerHTML = "try{remove();}catch{console.log('no p5');}";
                this.instance.appendChild(newS);   
    }

    componentDidUpdate() {

        this.props.project.scripts.forEach(s => {
            fetch('projects/' + this.props.project.dir + '/' + s).then((r) => {
                return r.text();
            }).then((js) => {
                //console.log(js);
                var newS = document.createElement('script');
                newS.type = 'text/javascript';
                newS.async = true;
                newS.innerHTML = js;
                this.instance.appendChild(newS);
               // this.instance.appendChild(scripts[0]);
            });
        });

    }

    fetchProject(){
        fetch('projects/' + this.props.project.dir + '/page.html').then((r) => {
            return(r.text());
        }).then((html) => {
            this.setState({'html':html});
        });
    }

    render() {
        return (
            <div className="project-page">
                <Link to="/">
                    <img className="close-icon" alt="Close" src={closeIcon}></img>
                </Link>
                <div ref={el => (this.instance = el)} dangerouslySetInnerHTML={{__html:this.state.html}}></div>
            </div>
        )
    }
}

export default Project
